body {
  margin: 0;
  font-family: sans-serif;
  background: #fafafa;
  color: #000;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}
.resizable-box-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;  /* full viewport width */
  height: 100vh; /* full viewport height */
  box-sizing: border-box;
}

.resize-stage, .canvas-stage {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resize-stage h1, .canvas-stage h3 {
  font-weight: normal;
}

.controls {
  display: flex;
  gap: 20px;
  margin: 10px 0;
}

.controls label {
  display: flex;
  flex-direction: column;
  font-size: 14px;
}

.controls input {
  width: 80px;
  margin-top: 5px;
  border: 1px solid #000;
  background: #fff;
  text-align: center;
}


.accept-button 
{
      position: fixed;
  border: 1px solid #000;
  background: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 10px;
}
.drawer-toggle {
      position: fixed;
  border: 1px solid #000;
  background: #fff;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 10px;
visible: hidden;
}
.background-controls {
position: fixed;
    top: 0;
    left: 0;
}

.accept-button:hover,
.drawer-toggle:hover {
  background: #f0f0f0;
}

.canvas-stage {
  position: relative;
}

.drawer {
    position: fixed;
    top: 0;
    left: 2400px;
    width: 220px;
    height: 100%;
    background: #fff;
    border-right: 1px solid black;
    transform: translateX(-220px);
    transition: transform 0.3s ease;
    padding: 10px;
    box-sizing: border-box;
}
.drawer.open {
  transform: translateX(0);
}

.thumbnail-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
}

.thumbnail-item {
  border: 1px solid #000;
  background: #fff;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.canvas-container {
  background: #fff;
}

.drawer.right.open {
  transform: translateX(0);
}
.drawer.left {
  position: absolute;
  top: 0;
  left: -350px;
  width: 220px;
  height: 100%;
  background: #fff;
  border-right: 1px solid black;
  transform: translateX(-220px);
  transition: transform 0.3s ease;
  padding: 10px;
  box-sizing: border-box;
}
.drawer.left.open {
  transform: translateX(0);
}


.drawer.right.open {
  transform: translateX(0);
}

.drawer-toggle.left {
  position: absolute;
  top: 10px;
  left: 220px;
  z-index: 10;
visibility: hidden;

}
.drawer-toggle.right {
  position: absolute;
  top: 10px;
  right: 220px;
  z-index: 10;
visibility: hidden;

}
button.export-button {
    border: 1px solid #000;
    background: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 140px;
    margin-top: 10px;
}
.layer-list, .thumbnail-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
}

.layer-item, .thumbnail-item {
  display: flex;
  align-items: center;
  gap: 10px;
  border: 1px solid #000;
  padding: 5px;
  background: #fff;
}

.layer-controls {
  display: flex;
  flex-direction: column;
}

.layer-controls button {
  border: 1px solid #000;
  background: #fff;
  cursor: pointer;
  width: 24px;
  height: 24px;
}

.layer-controls button:hover {
  background: #eee;
}
.font-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.font-item {
  border: 1px solid #000;
  background: #fff;
  cursor: pointer;
  padding: 5px;
}
.text-controls {
    position: absolute;
    top: 50px;
    right: -250px;
    background: rgb(255, 255, 255);
    border: 1px solid rgb(0, 0, 0);
    padding: 10px;
    z-index: 100;
}